body {
  @apply bg-body font-montserrat text-black-light;
}

.section {
  @apply py-10 md:py-[60px] xl:py-20;
}

.btn {
  @apply border border-black p-2 duration-300;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.section {
  @apply py-10 md:py-[60px] xl:py-20;
}

.section-title {
  @apply mb-6 text-2xl font-bold md:text-[32px] md:leading-9 xl:text-[44px] xl:leading-[1.27];
}

.input-wrap {
  @apply relative w-full font-medium;
}

.input-wrap-radio {
  @apply pb-2;
}

.input-wrap-half {
  @apply w-[calc((100%_-_1.5rem)/2)];
}
.input-wrap-half + .input-wrap-checkbox {
  @apply mt-6;
}

.input-label {
  @apply mb-1 inline-block text-[14px] font-medium leading-[1.2] md:mb-2 md:text-[16px] md:leading-[1.6];
}

.input-message {
  @apply resize-none;
}

.hero .input-field {
  @apply h-11 w-full rounded-lg border border-white bg-transparent p-2 px-4 text-[14px] font-medium leading-[1.2] text-white md:text-[16px] md:leading-[1.25];
}
.hero .input-field::placeholder {
  @apply text-green-placeholder font-medium;
}
.hero .input-field:focus {
  @apply border-accent outline-none;
}
.hero .input-field.is-invalid {
  @apply border-2 border-[#CA381F];
}

.modal .input-field {
  @apply h-11 w-full rounded-lg border border-black-border  p-2 px-4 text-[14px] font-medium leading-[1.2] text-black-dark md:text-[16px] md:leading-[1.25];
}
.modal .input-field::placeholder {
  @apply text-black-placeholder font-medium;
}
.modal .input-field:focus {
  @apply border-accent outline-none;
}
.modal .input-field.is-invalid {
  @apply border-2 border-[#CA381F];
}

.join .input-field {
  @apply h-11 w-full rounded-lg border border-[#B2B9CB] bg-white p-2 px-4 text-[14px] font-medium leading-[1.2] text-black-dark md:text-[16px] md:leading-[1.25];
}
.join .input-field::placeholder {
  @apply text-black-placeholder font-medium;
}
.join .input-field:focus {
  @apply border-accent outline-none;
}
.join .input-field.is-invalid {
  @apply border-2 border-[#CA381F];
}

.checkbox {
  @apply absolute left-0 top-0 inline-block h-6 w-6 rounded-full border border-gray-400 bg-contain bg-center bg-no-repeat;
}
[type=radio]:checked + .checkbox, [type=checkbox]:checked + .checkbox {
  @apply border-accent bg-check;
}

.input-label-checkbox {
  @apply relative inline-block cursor-pointer pl-10 text-[10px] font-medium leading-[1.4] md:text-[12px];
}
.input-label-checkbox > .is-invalid + .checkbox {
  @apply border-2 border-[#CA381F];
}

