.join .timer {
  @apply rounded-2xl bg-green-dark py-3;
}

.hero .timer {
  @apply rounded-2xl bg-white-100 py-3;
}

.modal .timer {
  @apply rounded-2xl bg-green-light py-3;
}

/*! purgecss start ignore */
.leeloo {
  @apply mx-auto mt-12 overflow-hidden;
}
.leeloo iframe {
  @apply mx-auto block;
}
.leeloo > div {
  width: 100%;
}
.leeloo > div .leeloo-lgt-form-wrapper .leeloo-lgt-form {
  width: 100% !important;
}
.leeloo .leeloo-lgt-form-wrapper {
  @apply block;
}
