  width: 100% !important;
}

/*! purgecss end ignore */
.progress-bar {
  @apply relative mt-6 h-[10px] w-full rounded-2xl;
}
.progress-bar .color {
  @apply absolute h-[10px] w-0 rounded-2xl bg-accent;
  animation: progress 4s infinite linear;
}

@keyframes progress {
  0% {
    width: 0%;
  }
  25% {
    width: 50%;
  }
  50% {
    width: 75%;
  }
  75% {
    width: 85%;
  }
  100% {
    width: 100%;
  }
}
.scroll-hidden {
  @apply overflow-hidden;
}

.steps-bar {
  @apply relative mx-[18px] flex justify-between border-b border-accent py-0 md:mx-10;
}
.steps-bar > .item.active {
  @apply border-none bg-[#FFB584] text-[#010101];
